<template>
	<div id="shop-select">
		<public-header title="选择店铺"></public-header>
		<div class="content">
			<div class="shop-item flex-lr" v-for="item in stores" :key="item.id" @click="onSelectShop(item.id)">
				<div class="info flex">
					<div class="logo-wrap">
						<img class="wh-100" :src="item.logo" alt="">
					</div>
					<div class="title ellipsis">{{item.title}}</div>
				</div>
				<i class="icon icon-right font-16 c-gray"></i>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				stores: [],
				showPreLoading: true,
			}
		},
		methods: {
			onLoad() {
				var that = this;
				var query = that.util.parseQuery();
				var from = query.from;
				getData({
					vue: that,
					url: 'manage/home/index',
					data: {
						nosid: 1
					},
					success: (res) => {
						let stores = res.stores;
						if(!from && stores && stores.length == 1) {
							that.util.setStorage('__sid', stores[0].id);
							if(that.util.isWeixin()) {
								that.util.jsUrl('wx:scanCode');
							} else {
								that.util.jsUrl('/pages/order/index');
							}

							/*var forwardUrl = that.util.getStorage('forwardUrl');
							if(forwardUrl) {
								window.location.href = forwardUrl;
							} else {
								that.util.jsUrl('/pages/order/index');
							}*/
							return;
						}
						if(stores && stores.length > 1) {
							that.stores = [...that.stores, ...stores];
							return;
						}
					}
				})
			},
			onSelectShop(sid) {
				this.util.setStorage('__sid', sid);
				if(that.util.isWeixin()) {
					that.util.jsUrl('wx:scanCode');
				} else {
					that.util.jsUrl('/pages/order/index');
				}
				/*var forwardUrl = this.util.getStorage('forwardUrl');
				if(forwardUrl) {
					window.location.href = forwardUrl;
				} else {
					this.util.jsUrl('/pages/order/index');
				}*/
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style >
	#shop-select .shop-item{
		padding: 10px 15px;
		margin-top: 10px;
		background-color: #fff;
		color: #333;
	}
	#shop-select .shop-item .info{
		width: 90%;
		font-size: 15px;
	}
	#shop-select .shop-item .info .title{
		flex: 1;
	}
	#shop-select .shop-item .logo-wrap{
		width: 40px;
		height: 40px;
		font-size: 0px;
		margin-right: 10px;
		border-radius: 5px;
		overflow: hidden;
	}
</style>


